.main-container {
    margin: 0 auto;
    background: rgb(206, 194, 194);
    height: calc(100vh);
}
.gameLogo{
  font-size: 2rem;
  color: white;
}
.callout{
  position: relative;
  margin-top: -1%;
}
.callbg {
  position: absolute;
  left: -18%;
  width: 105%;
}
.callball{
  position: absolute;
  left: -3%;
  top: 6%;
  width: 83%;
}
.userDetails{
  font-size: 2rem;
  color: white;
  text-align: center
}
.housie .userDetails {
  text-align: right;
}
.onlinePlayer {
  position: absolute;
  right: 5%;
  color: #fff;
  font-size: 1.4rem;
  top: 8.5%;
}
.calledNumber {
  color: #fff;
  position: absolute;
  font-size: 4rem;
  top: 2%;
  left: 9%;
  font-weight: 500;
}
.menuIcon img {
  float: right;
  width: 46%;
}
.housie .menuIcon img{
  width: 36%;
}
.shuffleIcon {
  text-align: center;
}
.shuffleIcon img{
  width :47%;
}
.playIcon {
  text-align: center;
}
.playIcon img{
  width :61%;
}
.onlineText {
  text-align: center;
  font-size: calc(0.5rem + 1vh);
  color:white;
}
.selectTitle{
  font-size: 2rem;
  color: white;
  text-align: center;
  font-weight: 500;
}
.ticket-number {
    width: 100%;
    height: 100%;
    border: 1px solid #ffca63;
    background-color: white;
    text-align: center;
    align-items: center;
    padding:2% !important;
  }
  .ticketRow {
    height: 33.5%;
  }

  .ticket-number span{
    font-size: calc(0.5em + 5vw);
    font-weight: 500;
    display: flex;
    height: 100%;
    margin: 0 auto;
    line-height: 0.5;
  }
  .tickerTitle {
    text-transform: uppercase;
    letter-spacing: 0px;
    font-size: calc(0.5rem + 2vh);
    transform: rotate(270deg);
    font-weight: 500;
    position: absolute;
    left: -9vh;
    top: 33%;
    color: white;
  }
  .ticketLabel {
    position: absolute;
    left: -5.5%;
    font-size: calc(0.5rem + 1vh);
    bottom: 0;
    font-weight: 500;
    letter-spacing: -0.1rem;
    bottom: -3%;
    font-weight: 600;
}
  
  .called {
    transition: all .5s ease-in-out;
    background-color: #e5e05e;
  }
  .number-wrapper {
    width: 100%;
  }

  .ticket-wrapper {
    margin-right: 2%;
    background-size: 100% 100%;
    height: 18%;
    border: 10px solid #ffca63;
    border-radius: 3vh;
    border-left-width: 5vh;
    margin-left: 3%;
    margin-top: 0%;
    position: relative;
    background-color: #ffca63;
  }
  .housie-wrapper {
    width: 100%;
    height: 39%;
    position: relative;
    top: 0%;
  }
  .housieRow {
    width: 100%;
    height: 11%;
    margin-bottom: 3px !important;
  }
  .housie-number {
    width: 42px;
    height: 42px;
    border: 2px solid #d10000;
    background-color: #082657;
    text-align: center;
    border-radius: 50%;
    margin-left: 31px !important;
  }
  .housie-number span {
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 1.2;
    color: #5688e9;
  }
  .housie-number-called {
    width: 42px;
    height: 42px;
    border: 2px solid #d10000;
    background-color: #d10000;
    text-align: center;
    border-radius: 50%;
    margin-left: 31px !important;
  }
  .housie-number-called span {
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 1.2;
    color: #fff;
  }
  .first-ticket-row > div:last-child {
    border-top-right-radius: 2vh;
  }
  .last-ticket-row > div:last-child {
    border-bottom-right-radius: 2vh;
  }
  .first-ticket-row > div:first-child {
    border-top-left-radius: 2vh;
  }
  .last-ticket-row > div:first-child {
    border-bottom-left-radius: 2vh;
  }
  
  .ticket-container > div:first-child {
    padding: 10px 0;
  }
  .gamebg {
    background-image: url(../assets/img/phone_bg.png);
    background-size: 100% 100%;
    height: 100%;
    padding:2%;
  }

  .ticketbg {
    background-image: url(../assets/img/ticket_bg.png);
  }
  .blankTicketNumber {
    background-color: #ffeecd;
  }
  .bottomBar{
    color: white;
    position: absolute;
    height: 85px;
    bottom: 0px;
    width: 100%;
    text-align: center;
    align-items: center;
    font-size: 1.3rem;
    background-color: #451c74;
    display: flex;
  }
  .bottomBar .col{
    border-radius: 50px;
    margin: 4%;
    font-size: 2rem;
    line-height:2;
  }
  .bottomBar .col.selected{
    background-color: #d2d1e3;
    color:#451c74;
    font-weight: 500;
  }
  .betCard {
    position: absolute;
    bottom: 50px;
    background-color: #e8edfc;
    height: 28%;
    width: 98%;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    left: 1%;
    box-shadow: 0px 0 10px rgba(69, 28, 116, 0.8);
  }
  .betCard2 {
    position: absolute;
    bottom: 50px;
    background-color: #e8edfc;
    height: 35%;
    width: 98%;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    left: 1%;
    box-shadow: 0px 0 10px rgba(69, 28, 116, 0.8);
  }
  .betCard3 {
    position: absolute;
    bottom: 50px;
    background-color: #e8edfc;
    height: 42%;
    width: 98%;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    left: 1%;
    box-shadow: 0px 0 10px rgba(69, 28, 116, 0.8);
  }
  .cardTitle {
    font-size: 2.5rem;
    font-weight: 500;
    color:#48108d;
  }
  .cardTopImg{
    margin-top: 2%;
    width: 71px;
    margin-left: 50%;
    height: 37px;
  }
  .cardTopImg2 {
    margin-left: 44%;
    margin-top: 2%;
    width: 71px;
    height: 37px;
  }
  .cardTopImg3 {
    margin-left: 40%;
    margin-top: 2%;
    width: 71px;
    height: 37px;
  }
  .betTitle {
    background-color:#e1d4f4;
    padding: 1%;
    text-align: center;
    font-size: 1.5rem;
  }
  .betValues {
    font-size:1.5rem;
    position: relative;
  }
  .betValues .betvalue {
    position: absolute;
    left:17%;
    top:16%;
  }
  .betValues img {
    width:90%;
  }
  .betValues .betTicketValue {
    margin-top:1%;
  }
  .betValues .betTicketValueRs {
    margin-top:1%;
    text-align: center;
  }
  .betTitle .back {
    font-size:1.5rem;
    margin-left: 20%;
  }
  .betTitle .paysTitle {
    font-size:1.5rem;
    margin-left: 27%;
  }
  .betModal {
    height: 74vh;
    background-color: #f0edff;
    border-top-left-radius: 21px;
    border-top-right-radius: 21px;
    border-bottom-right-radius: 21px;
    border-bottom-left-radius: 21px;
  }
  .betModal .modal-content {
    background-color:transparent !important;
    border:0px !important;
  }
  .betModalBody {
    background-color: #92c9ff;
    border-top-left-radius: 21px;
    border-top-right-radius: 21px;
    padding-bottom: 1%;
    border-bottom-right-radius: 21px;
    border-bottom-left-radius: 21px;
  }
  .betLayModalBody {
    background-color: #ffb7d9;
    border-top-left-radius: 21px;
    border-top-right-radius: 21px;
    padding-bottom: 1%;
    border-bottom-right-radius: 21px;
    border-bottom-left-radius: 21px;
  }
  .betLayModalBody .backlayTitle {
    font-size: 2.5rem;
    text-align: center;
    font-weight: bold;
    color: #005894;
  }
  .betLayModalBody .backlayTitle .layTitle{
    padding: 2%;
    color:#8f1b98;
  }
  .betLayModalBody .backlayTitle .backTitle{
    background-color: #005894;
    color: #fff;
    padding: 2%;
    border-top-left-radius: 20px;
  }
  .modaltitle{
    text-align: center;
  }
  .bettitle {
    text-align: center;
  }
  .bettitle .title {
    font-size: 2rem;
    font-weight: bold;
    color: #005894;
    letter-spacing: 2px;
  }
  .betLayModalBody .bettitle .title {
    font-size: 2rem;
    font-weight: bold;
    color: #d2519f;
    letter-spacing: 2px;
  }
  .modaltitle .title{
    font-size: 2.2rem;
    font-weight: bold;
    color: #4c1f90;
    letter-spacing: 2px;
  }
  .betModalBody .backlayTitle {
    font-size: 2.5rem;
    text-align: center;
    font-weight: bold;
    color: #005894;
  }
  .betModalBody .backlayTitle .backTitle{
    padding: 2%;
  }
  .betModalBody .backlayTitle .layTitle{
    background-color: #d2519f;
    color: #fff;
    padding: 2%;
    border-top-right-radius: 20px;
  }
  .modal-body .ticketRow{
    font-size: 2.5rem;
    text-align: center;
    font-weight: 500;
    color:#005995;
  }
  .betLayModalBody .ticketRow {
    color:#67176d;
  }
  .modal-body .ticketRow .ticket1{
    margin: 3%;
    border-radius: 15px;
    background-color: #fff;
  }
  .modal-body .ticketRow .ticket2{
    margin: 3%;
    border-radius: 15px;
    background-color: #c9e4ff;
  }
  .betLayModalBody .ticketRow .ticket2{
    background-color: #ffdbec;
  }
  .fotterRow {
    font-size: 2.2rem;
    text-align: center;
    font-weight: 500;
    color:#4c1e8f;
  }
  .fotterRow .cancelbtn {
    margin: 1%;
    border-radius: 15px;
    border:2px solid #4c1e8f; 
  }
  .fotterRow .submitbtn {
    margin: 1%;
    border-radius: 15px;
    background-color: #4c1e8f;
    color:#fff;
  }
  
  .betticketRow {
    font-size: 2.2rem;
    text-align: center;
    font-weight: 500;
    color:#005995;
  }
  .betLayModalBody .betticketRow {
    color:#67176d;
  }
  .betticketRow .betticket1 {
    margin: 1%;
    border-radius: 15px;
    background-color: #fff;
  }
  .betticketRow .betticket2 {
    margin: 1%;
    border-radius: 15px;
    background-color: #c9e4ff;
  }
  .betLayModalBody .betticketRow .betticket2 {
    margin: 1%;
    border-radius: 15px;
    background-color: #fff;
  }
  .rowDivider {
    height: 2px;
    width: 106%;
    background-color: #005894;
  }
  .betLayModalBody .rowDivider {
    height: 2px;
    width: 106%;
    background-color: #d2519f;
  }
  .globalmenu{
    position: fixed;
    width: 47%;
    background-color: #14133c;
    height: 100vh;
    margin-top: -2%;
    right: 0px;
    z-index: 1;
    text-align: center;
    transition:right .5s;
  }
  .globalmenu .menutitle {
    font-size: 3rem;
    color: #ffcb63;
  }
  .globalmenu .closebtn{
    width: 76%;
    margin-top: 26%;
  }
  .globalmenu .menuitem {
    border-bottom: 1px solid #070715;
    /* margin: 0%; */
    padding: 2%;
    border-top: 1px solid #070715;
  }
  .globalmenu .menuitem img{
    width: 13%;
    margin-right: 4%;
  }
  .globalmenu .menuitem span {
    color: #4970c5;
    font-size: 2rem;
  }
  .hideGlobalMenu {
    right: -47%;
  }
  .showGlobalMenu {
    right: 0px;
  }
  @media screen and (max-width: 480px) {
    .bottomBar{
      height: 50px;
    }
    .bottomBar .col{
      font-size: 1rem;
    }
    .ticket-number{
        padding:1.5% !important;
    }
    .ticket-number span {
      font-size: calc(0.5em + 4.5vw);
      font-weight: 500;
      line-height: 0.81;
    }
    .ticketLabel{
      left: -7.5%;
      font-size: 0.8rem;
      bottom: -4%;
    }
    .tickerTitle {
      font-size: calc(0.5rem + 1.5vh);
      left: -8.5vh;
    }
    .selectTitle{
      font-size: 1.5rem;
    }
    .gameLogo, .userDetails {
      font-size: 1rem;
    }
    .cardTitle {
      font-size: 1.5rem;
    }
    .cardTopImg {
      width: 48px;
      margin-left: 39%;
      height: 28px;
      margin-top: 0;
    }
    .cardTopImg2 {
      width: 48px;
      margin-left: 33%;
      height: 28px;
      
    }
    .cardTopImg3 {
      width: 48px;
      margin-left: 29%;
      height: 28px;
      margin-top: 1%;
    }
    .betTitle {
      padding: 2px;
      font-size: 1rem;
    }
    .betValues {
      font-size: 1.1rem;
    }
    .betValues .betvalue {
      position: absolute;
      left: 16%;
      top: 7%;
    }
    .betCard {
      height:23%;
    }
    .betCard2 {
      height:31%;
    }
    .betCard3 {
      height:38%;
    }
    .housie-number {
      width: 30px;
      height: 30px;
      border: 2px solid #d10000;
      background-color: #082657;
      text-align: center;
      border-radius: 50%;
      margin-left: 11px !important;
      line-height: 1;
    }
    .housieRow {
      margin-bottom: 4px !important;
    }
    .housie-number span {
      font-size: 0.9rem;
      font-weight: bold;
      color: #5688e9;
    }
    .housie-number-called {
      width: 30px;
      height: 30px;
      border: 2px solid #d10000;
      background-color: #d10000;
      text-align: center;
      border-radius: 50%;
      margin-left: 11px !important;
      line-height: 1;
    }
    .housie-number-called span {
      font-size: 0.9rem;
      font-weight: bold;
      color: #fff;
    }
    .onlinePlayer {
      font-size:1rem;
    }
    .calledNumber {
      font-size: 2.3rem;
    }
    .modal-body .fotterRow {
      font-size:1.5rem;
      margin-top: 0.5rem !important;
    }
    .modal-body .betticketRow {
      font-size:1.5rem;
    }
    .modal-body .title {
      font-size: 2rem;
    }
    .modal-body .backlayTitle {
      font-size: 1.5rem;
    }
    .modal-body .ticketRow {
      font-size: 1.5rem;
    }
}